<template>
  <div class="search">
    <div class="hot" v-if="isHot">
      <div class="title">热搜榜</div>
      <ul class="rank">
        <li v-for="(hot, index) in hots" :key="index">
          <div class="num" :class="{ active: index < 3 }">{{ index + 1 }}</div>
          <div class="name" @click="navigate(hot.searchWord)">
            {{ hot.searchWord }}
          </div>
        </li>
      </ul>
    </div>
    <div class="searching" v-else>
      <ul class="content">
        <li
          v-for="search in searchs"
          :key="search.id"
          @click="navigate(search)"
        >
          {{ search }}
        </li>
      </ul>
    </div>

    <div class="history" v-if="searchHistory.length">
      <div class="title">历史记录</div>
      <ul v-if="searchHistory">
        <li v-for="(item, index) in searchHistory" :key="index">
          <div class="name" @click="navigate(searchHistory[index])">
            {{ searchHistory[index] }}
          </div>
          <div class="delete" @click="deleteHistory(index)">
            <van-icon name="cross" />
          </div>
        </li>
      </ul>
      <div class="clear-all" @click="clearAll">
        清空所有历史记录
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Search",
  props: ["msg", "isHot"],
  computed: {
    ...mapState(["currentmusic", "paused", "searchs", "searchHistory"]),
  },
  data() {
    return {
      hots: [], // 热搜榜
      timer: null, //延时器
    };
  },
  watch: {
    msg() {
      if (!this.msg.trim()) {
        // this.isHot = true;
        this.$emit("update:isHot", true);
        return;
      }
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if (this.msg == "") {
          return;
        }
        this.axios
          .get(`/search?keywords=${this.msg}&limit=30`)
          .then((response) => {
            // this.searchs = response.data.result.songs;
            // this.$store.commit("updateSearchs", response.data.result.songs);
            var ss = response.data.result.songs;
            let name = [];
            let author = [];

            ss.filter((ele) => {
              // 歌名
              if (name.indexOf(ele.name) == -1 && ele.name.includes(this.msg)) {
                name.push(ele.name);
              }
              // 歌手
              ele.artists.forEach((ele) => {
                if (
                  author.indexOf(ele.name) == -1 &&
                  ele.name.includes(this.msg)
                ) {
                  author.push(ele.name);
                }
              });
            });
            // this.searchs = name.concat(author);
            this.$store.commit("updateSearchs", name.concat(author));
            // console.log(this.searchs);
          })
          .catch(() => {});
      }, 500);
    },
  },
  created() {
    this.axios.get("/search/hot/detail").then((response) => {
      this.hots = response.data.data.slice(0, 10);
    });
    //   .catch((err) => {});
  },
  methods: {
    navigate(name) {
      this.$emit("update:msg", name);
      this.$router.push({ name: "Song", query: { name: name } });
    },
    deleteHistory(index) {
      var history = this.searchHistory.concat();
      history.splice(index, 1);
      // console.log(history);

      this.$store.commit("resetSearchHistory", history);
    },
    clearAll() {
      this.$store.commit("resetSearchHistory", []);
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  width: 100vw;
  height: calc(100vh - 96px);
  margin-top: 46px;
  background-color: #fff;
  position: fixed;
  z-index: 1000;
  overflow-x: scroll;
  .hot {
    padding: 0 10px;
    margin-top: 15px;
    .title {
      font-size: 14px;
    }
    .rank {
      display: flex;
      flex-wrap: wrap;
      li {
        margin-top: 10px;
        flex-basis: 50%;
        display: flex;
        .num {
          margin-right: 4px;
          flex-basis: 15%;
          text-align: center;
          &.active {
            color: #f21b19;
          }
        }
        .name {
          font-size: 14px;
          flex: 1;
          word-wrap: break-word;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1; /*行数*/
          -webkit-box-orient: vertical;
        }
      }
    }
  }
  .searching {
    position: absolute;
    top: 0;
    width: 100vw;
    z-index: 10;
    background-color: #fff;
    height: calc(100vh - 96px);
    overflow-y: auto;
    .content {
      li {
        text-align: left;
        padding: 5px 0;
        border-bottom: 1px solid #eee;
        line-height: 25px;
        text-indent: 12px;
        font-size: 12px;
        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
    }
  }
  .history {
    padding: 0 10px;
    margin-top: 30px;
    font-size: 14px;
    ul {
      margin-top: 10px;
      li {
        display: flex;
        line-height: 35px;
        margin: 0 10px;
        border-bottom: 1px solid #ddd;
        &:first-of-type {
          border-top: 1px solid #ddd;
        }
        .name {
          flex: 1;
        }
        .delete {
          display: flex;
          align-items: center;
        }
      }
    }
    .clear-all {
      margin-top: 10px;
      color: #a930d9;
      text-align: center;
    }
  }
}
</style>